import { FC } from "react"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import ASelect from "@/shared/ui/Inputs/Select/ASelect"
import { ContactEventType, eventContactActions } from "../model/EventContactSlice"

interface EventContactProps {
    type: ContactEventType
}



export const EventContact: FC<EventContactProps> = ({
    type

}) => {

    const contacts = useAppSelector(state => state.contact.contacts)
    // const current = useAppSelector(state => state.contact.current.contact)
    const plan = useAppSelector(state => state.contact.current.plan)
    const report = useAppSelector(state => state.contact.current.report)
    const isReport = type == 'report'
    let current = plan
    if (isReport) {
        current = report
    }

    const dispatch = useAppDispatch();
    const setCurrent = (
        type: ContactEventType,
        contactId: string | number
    ) => {
        const id = contactId as number
        dispatch(
            eventContactActions
                .setCurrentContact({
                    type,
                    contactId: id
                })
        )
    }
    // return <EVCard
    //     title="Контакт"
    //     width={12}
    //     children={
    //         <div>
    //             Контакт
    //             <ASelect
    //                 current={current}
    //                 items={contacts}
    //                 withLabel
    //                 label="Контакт"
    //                 nameForHandler="contact"
    //                 handleChange={() => alert('contact')}
    //             />
    //         </div>
    //     }
    //     size="medium"
    //     key={1}
    // />
    return <ASelect
        current={current}
        items={contacts}
        withLabel
        label={isReport ? 'С кем велись переговоры' :"Контакт"}
        nameForHandler={type}
        handleChange={setCurrent}
    />
}

