import { FC, useEffect, useState } from "react"
import { EV_REPORT_PROP } from "../../EventReport/type/event-report-type"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { eventReportActions } from "../../EventReport"
import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"
import AText from "@/shared/ui/Inputs/Text/AText"
import { eventActions } from "@/processes/event"
import { EV_PLAN_CODE, EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type"
import { EV_PLAN_STATE_ITEM } from "@/entities/EventPlan/model/EventPlanSlice"
import { DEPARTAMENT_STATE_PROP } from "@/features/Departament/type/department-type"
import { EventContact } from "@/entities/EventContact"
import useWindowSize from "@/app/lib/hooks/display"


export const Comment: FC = () => {
    const [isRequiredError, setIsRequiredError] = useState(false)

    const handleOnNameFocus = () => {
        if (isRequiredError) {
            setIsRequiredError(false)
        }


    }

    const dispatch = useAppDispatch();
    const error = useAppSelector(state => state.event.errors.current.comment)

    const handleChange = (value: string) => {
        if (error) {
            dispatch(
                eventActions
                    .setError(
                        {
                            code: EV_REPORT_PROP.COMMENT,
                            value: ''
                        }
                    )
            )
        }
        dispatch(
            eventReportActions
                .setReportProp(
                    {
                        propName: EV_REPORT_PROP.COMMENT,
                        value
                    }
                )
        )
    }

    const planState = useAppSelector(state => state.eventPlan)

    const type = planState[EV_PLAN_PROP.TYPE] as EV_PLAN_STATE_ITEM
    const currentDepartmentMode = useAppSelector(state => state.department[DEPARTAMENT_STATE_PROP.MODE].current)
    const isTmcMode = currentDepartmentMode.code === 'tmc'
    const isPresentationPlaning = type.current.code === EV_PLAN_CODE.PRESENTATION
    const isBigComment = isTmcMode && isPresentationPlaning
    const { width } = useWindowSize();
    const [isSmallDisplay, setIsSmallDisplay] = useState(width < 577)
    useEffect(() => {
        const isSmall = width < 577
        setIsSmallDisplay(
            isSmall
        )

    }, [
        width
    ])
    return (
        <EVCard title={'Отчетность'} width={12} size={isSmallDisplay ? "small" : "big"}>

            {
                !isTmcMode && <EventContact type="report" />
            }
            <AText
                label={'Опишите кратко что было'}
                height={isSmallDisplay ? 4 : isTmcMode ? 11 : 7}
                handleChange={handleChange}
                nameForHandler={EV_REPORT_PROP.COMMENT}
                errorMessage={error}
            />



        </EVCard>

    )
}
