import { AppDispatch, AppGetState } from "@/app/model/store";
import { bitrixAPI } from "@/app/services/bitrix-general-api";
import { eventContactActions } from "./EventContactSlice";
import { BXTask } from "@/app/types/bitrix/bitrix-type";

export const getCompanyContacts = (
    // bxCompany: BXCompany, 
    companyId: number
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {


        const contactsFrom = await bitrixAPI.getMethod(
            'crm.company.contact.items.get',
            {
                id: companyId

                // SELECT: ['NAME', 'PHONE', 'ID']
            },
            'april-dev.bitrix24.ru'
        );
        //@ts-ignore
        const contactIds = contactsFrom.map(contact => contact.CONTACT_ID)

        const contacts = await bitrixAPI.getMethod(
            'crm.contact.list',
            {
                filter: {
                    'ID': contactIds
                },
                select: [
                    'NAME', 'PHONE', 'ID', 'EMAIL', 'POST', 'LAST_NAME'
                ]
                // SELECT: ['NAME', 'PHONE', 'ID']
            },
            'april-dev.bitrix24.ru'
        );
        
        dispatch(
            eventContactActions
                .setFetchedContacts({
                    contacts
                })
        )
        // if (contacts && contacts.length) {

        //     const contactId = contacts[0]['CONTACT_ID']
        //     const contact = await bitrixAPI.getMethod(
        //         'crm.company.contact.items.get',
        //         { id: contactId, },
        //         'april-dev.bitrix24.ru'
        //     );
        // }
        
    }

export const setCurrentReportContact = (
    currentTask: BXTask | null
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
        if (currentTask) {


            if (currentTask.ufCrmTask) {
                const taskContactcrmString = currentTask.ufCrmTask.find(crm => crm.startsWith('C') && !crm.startsWith('CO'));

                if (taskContactcrmString) {
                    const idString = taskContactcrmString.split('_')[1]; // '678'
                    const contactId = Number(idString)
                    if (contactId) {
                        dispatch(
                            eventContactActions
                                .setInitCurrentContact(
                                    {
                                        type: 'init',
                                        contactId
                                    }
                                )
                        )
                    }
                }
            }

        } else {
            dispatch(
                eventContactActions
                    .setInitCurrentContact(
                        {
                            type: 'init',
                            contactId: null
                        }
                    )
            )
        }
    }